//
// Alert
// --------------------------------------------------

// Base class
.alert {
  display: block;
  position: relative;
  padding: $grid-vertical-step;
  border: 5px solid transparent;
  border-radius: $border-radius-base;
  background: {
    position: center;
    repeat: no-repeat;
    size: cover;
    clip: padding-box;
  }
  i {
    display: inline-block;
    margin-top: -2px;
    vertical-align: middle;
  }
  > *,
  h1, h2, h3, h4, h5, h6, p, ul, ol, a {
    &:last-child:not(.btn) { margin: 0; }
  }
  &::before {
    display: block;
    position: absolute;
    top: -6px;
    right: -6px;
    bottom: -6px;
    left: -6px;
    border: 1px solid transparent;
    border-radius: $border-radius-base;
    content: '';
    z-index: -1;
  }

  // Alert Dismissible
  .alert-dismissible {
    transition: opacity .4s;
    &.fade.show { opacity: 1; }
  }
  .alert-close {
    display: block;
    position: absolute;
    top: 10px;
    right: 12px;
    font: {
      family: feather;
      size: $font-size-sm;
    }
    cursor: pointer;
    &::before { content: '\e117'; }
  }
}

// Color Variants
.alert-default {
  @include alert-variant($gray-dark, rgba($gray-dark, .05), rgba($gray-dark, .13));
}
.alert-primary {
  @include alert-variant($primary, rgba($primary, .12), rgba($primary, .25));
}
.alert-info {
  @include alert-variant($info, rgba($info, .12), rgba($info, .3));
}
.alert-success {
  @include alert-variant(darken($success, 3%), rgba($success, .12), rgba($success, .25));
}
.alert-warning {
  @include alert-variant(darken($warning, 3%), rgba($warning, .12), rgba($warning, .25));
}
.alert-danger {
  @include alert-variant($danger, rgba($danger, .12), rgba($danger, .25));
}

// With Background Image
.alert-image-bg {
  border: 0;
  &::before { display: none; }
}

// Toast Notifications
.iziToast {
  border: 1px solid $border-color;
  background: $gray-lighter;
  &::after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    box-shadow: 0 8px 10px -4px rgba($black, .2);
    content: '';
    z-index: -1;
  }
  > .iziToast-body { margin-left: 15px; }
  > .iziToast-close {
    width: 40px;
    transition: opacity .25s;
    background: none;
    background: {
      position: center;
      color: transparent;
      image: url(data:image/svg+xml;utf8;base64,);
      size: 8px;
      repeat: no-repeat;
    }
  }
  &.iziToast-info {
    border-color: rgba($info, .3);
    background-color: lighten($info, 33%);
    color: darken($info, 5%);
    > .iziToast-close {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }
  }
  &.iziToast-success {
    border-color: rgba($success, .3);
    background-color: lighten($success, 36%);
    color: darken($success, 6%);
    > .iziToast-close {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }
  }
  &.iziToast-warning {
    border-color: rgba($warning, .3);
    background-color: lighten($warning, 30%);
    color: darken($warning, 3%);
    > .iziToast-close {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }
  }
  &.iziToast-danger {
    border-color: rgba($danger, .3);
    background-color: lighten($danger, 30%);
    color: $danger;
    > .iziToast-close {
      background-image: url(data:image/svg+xml;utf8;base64,);
    }
  }
}
.toast.position-fixed {
  min-width: 300px;
  z-index: 9900;
  &.top-center {
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
  }
  &.top-right {
    top: 30px;
    right: 30px;
  }
  &.top-left {
    top: 30px;
    left: 30px;
  }
  &.bottom-center {
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
  }
  &.bottom-right {
    bottom: 30px;
    right: 30px;
  }
  &.bottom-left {
    bottom: 30px;
    left: 30px;
  }
  @media (max-width: $screen-xs) {
    &.top-right,
    &.top-left,
    &.bottom-right,
    &.bottom-left {
      right: auto;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
